@media screen {
    :root {
        --no-print-visibility: visible;
    }

    .label {
        border-left: 1px dashed lightgray;
        border-right: 1px dashed lightgray;

    }
}

@media print {
    :root {
        --no-print-visibility: hidden;
    }

    .label {
        border: none;
    }
}

iframe {
    border: none;
}

.print-preview {
    --no-print-visibility: hidden;
}

.no-print {
    visibility: var(--no-print-visibility);
}

.label {
    margin: 0 auto;
    width: 3in;
    font-size: small;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.label table {
    margin: auto 0.05in;
}

.field {
    position: absolute;
}

tr.heading {
    text-align: center;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
}

tr.separated {
    border-top: 1px solid black;
}

tr.title {
    font-weight: bold;
}

tr.subtitle {
    color: white;
    background-color: black;
}

.table-footer {
    text-align: center;
}

tr.section-start {
    border-top: 2px solid black;
}